@use 'base/mixins' as m;
@use 'base/variables' as v;

.boletos{
    padding: 5rem 0;
    h3{
        margin-bottom: 1rem;
        text-align: center;
        color: v.$blanco;

    }
    .contenedor-boletos{
        @include m.tablet {
            @include m.grid(2, 5rem);
        }
    
}
}

.pase{
    .nombre-pase{
        
            text-align: center;
            color: v.$blanco;
            font-size: 2.6rem;;
    
    }
    &-header,
    &-footer{
        padding: .5rem;
        p{
            text-align: center;
            color: v.$blanco;
            margin: 0; 
        }
    }

    &-header{
        background-color: v.$rosa;
        p{
             text-transform: uppercase;
             font-size: 1.6rem;
             font-weight: 700;
        }
       
    }
    &-body{
        background-color: v.$blanco;
        ul{
            @include m.resetear-lista;
            li, 
            :nth-child(odd){
                background-color: v.$blanco;
                padding: 2rem;
            }
        }
    }
    &-footer{
        background-color: v.$morado;
        p{
            font-size: 2.4rem;
            font-weight: 700;
        }
    }

}


