@use 'base/variables' as v;
@use 'base/mixins' as m;

.sobre-festival{
    padding: 5rem;
    @include m.tablet {
        @include m.grid(2,5rem);
    }
    .imagen img{
        border-radius: 15px ;
    }
}

.contenido-festival{
    display: flex;
    flex-direction: column;
    justify-content: center;

    margin-top: 5rem;
    @include m.tablet{
        margin-top: 0;
    }
    h2{

        margin-bottom: 2rem;
    }
    .fecha{
        color: v.$rosa;
        font-weight: 700;
    }
}